.orange-text {
    color: orange;
}

.blue-text {
    color: #00b7ff;
}

.small-text {
    font-size: 15px;
}

.medium-text {
    font-size: 30px;
    font-weight: bolder;
}

.bold {
    font-weight: 500;
}

.bolder {
    font-weight: bolder;
}

.berlin {
    font-family: "Berlin Sans FB";
}

.arial {
    font-family: Arial, Helvetica, sans-serif;
}

.auto-height {
    min-height: 95vh;
    padding-bottom: 5vh;
    height: auto;
}

.auto-width {
    min-width: 80vw;
    width: auto;
}

.cursor {
    cursor: pointer;
}

.no-justify{
    justify-content: unset !important;
}

.no-align {
    align-items: unset !important;
}

.align {
    align-items: center !important;
}

.align-center {
    text-align: center !important;
}

.align-left {
    text-align: left !important;
}

.justify {
    justify-content: center !important;
}

.aboutme {
    padding: 30px;
    font-size: 18px;
    font-family: "Berlin Sans FB";
    color: black;
}

.myImage {
    object-fit: contain;
    align-self: flex-start;
    margin-left: 30px;
    width: 150px;
    border-radius: 50%;
}